<template>
<div id="rentCar">
   <h2>商家如何设置子账号及下级代理？</h2>
  <p>在e换电租车商家后台-工作台点击账号管理。</p>
  <p>在此模块可分别创建门店管理员、下级代理商。</p>
  <p>门店管理员可协助商家管理门店，不可查看工作台数据看板、车辆管理、对账中心、用户管理模块，其它权限由商家灵活配置。</p>
  <p>下级代理商由总账号配置，下级代理可创建门店单独运营，可发展下级代理。</p>
  <p>换电套餐天数由商家配置，子代及其下级代理不可提现。</p>
  <p>门店管理员、下级代理账号支持编辑、停用，停用的账号支持删除。</p>
  <p>停用及删除的门店管理员账号、下级代理账号无法登录。</p>
  <div class="imgDiv">
    <img src="../../assets/m18.png" alt="">
  </div>
</div>
</template>
<script>
    export default {
        name: "rentCar"
    }
</script>
<style scoped lang="scss">

</style>



